.gpt3Features {
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-direction: row;
}

.gpt3Features-heading {
    /* flex: 2; */
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-direction: column;
    text-align: left;
    margin-right: 5rem;
}

.gpt3Features-heading h1 {
    font-family: var(--font-family);
    font-size: 34px;
    font-weight: 800;
    line-height: 45px;
    letter-spacing: 0em;
    text-align: left;
}

.gpt3Features-heading p {
    font-family: var(--font-family);
    font-size: 16px;
    font-weight: 500;
    line-height: 30px;
    letter-spacing: 0em;
    text-align: left;
    color: rgba(255, 138, 113, 1);
    margin-top: 1rem;
}
.gpt3Features-container{
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    flex: 1.5;
}
@media  screen and (max-width: 990px) {
    .gpt3Features{
        flex-direction: column;
    }
    .gpt3Features-heading{
        margin: 0 0 2rem 0;
    }
    
}
@media  screen and (max-width: 550px) {
    .gpt3Features-heading{
        /* margin: 0 0 2rem 0; */
        font-size: 28px;
        line-height: 38px;
    }
    .gpt3Features-container_feature{
        flex-direction: column;
    }
    /* .gpt3Features-container{
        flex: 1;
    } */
    
}