 .gpt3CTA {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border: 1px solid white;
    color: white;
    padding: 2rem;
    margin: 4rem;
    border-radius: 1rem;
    background: var(--gradient-bar);
}

.gpt3CTA-content p {
    font-family: var(--font-family);
    font-size: 12px;
    font-weight: 500;
    line-height: 30px;
    color: rgba(14, 14, 14, 1);
}

.gpt3CTA-content h3 {
    font-family: var(--font-family);
    font-size: 24px;
    font-weight: 800;
    line-height: 45px;
    letter-spacing: 0em;
    text-align: left;
    color: black;
}

.gpt3CTA-btn {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 2rem;
}

.gpt3CTA-btn button {
    color: white;
    background: black;
    border-radius: 40px;
    font-family: var(--font-family);
    padding:  5px 15px;
    font-size: 12px;
    font-weight: 700;
    line-height: 30px;
    letter-spacing: 0em;
    text-align: left;
    border: none;
    outline: none;
    cursor: pointer;
    min-width: 150px;
}
@media screen and (max-width: 650px){
    .gpt3CTA{
        flex-direction: column;
    }
    .gpt3CTA-btn{
        margin: 2rem 0 0 ;
    }
} 
@media screen and (max-width: 550px){
    .gpt3CTA{
        margin: 4rem 2rem;
    }
    .gpt3CTA-content h3{
        font-size: 18px;
        line-height: 32px;
    }
    .gpt3CTA-btn button{
        font-size: 14px;
        line-height: 28px;
    }
} 