.gpt3Blog-container-article {
    width: 100%;
    height: 100%;

    display: flex;
    flex-direction: column;
    background: var(--color-footer);
}

.gpt3Blog-container-article-img {
    width: 100%;
    height: 100%;
    background: var(--color-footer);
}

.gpt3Blog-container-article-img img {
    width: 100%;
    height: 100%;
}

.gpt3Blog-container-article-content {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 1rem 1.5 rem;
    height: 100%;
}

.gpt3Blog-container-article-content p {
    font-family: var(--font-family);
    font-size: 12px;
    font-weight: 700;
    line-height: 35px;
    letter-spacing: 0em;
    text-align: left;
    color: rgba(255, 255, 255, 1);
}

.gpt3Blog-container-article-content h3 {
    font-family: var(--font-family);
    font-size: 25px;
    font-weight: 800;
    line-height: 30px;
    letter-spacing: 0em;
    text-align: left;
    color: white;
    margin-bottom: 5rem;
    cursor: pointer;
}

.gpt3Blog-container-article-content p:last-child {
    font-family: var(--font-family);
    font-size: 12px;
    font-weight: 700;
    line-height: 35px;
    letter-spacing: 0em;
    text-align: left;
    color: white;
    cursor: pointer;
}
@media screen and (max-width:550px){
    .gpt3Blog-container-article-content h3{
        font-size: 18px;
        line-height: 25px;
    }
}