.gpt3WhatGp{
    display: flex;
    /* color: white; */
    flex-direction: column;
    padding: 2rem;
    /* background: var(--color-footer); */

     /* ff 3.6+ */
     /* background:-moz-radial-gradient(circle at 30% -100%, #042c54 25%, rgba(4, 44, 84, 1) 85%, rgba(27, 120, 222, 1) 100%); */

     /* safari 5.1+,chrome 10+ */
     /* background:-webkit-radial-gradient(circle at 30% -100%, #042c54 25%, rgba(4, 44, 84, 1) 85%, rgba(27, 120, 222, 1) 100%); */
 
     /* opera 11.10+ */
     /* background:-o-radial-gradient(circle at 30% -100%, #042c54 25%, rgba(4, 44, 84, 1) 85%, rgba(27, 120, 222, 1) 100%); */
 
     /* ie 10+ */
     /* background:-ms-radial-gradient(circle at 30% -100%, #042c54 25%, rgba(4, 44, 84, 1) 85%, rgba(27, 120, 222, 1) 100%); */
 
     /* global 92%+ browsers support */
     background:radial-gradient(circle at 30% -100%, #042c54 25%, rgba(4, 44, 84, 1) 85%, rgba(27, 120, 222, 1) 100%);
 
     filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
}
.gpt3WhatGp-feature{
    display: flex;
}
.gpt3WhatGp-feature .gpt3__features-container__feature{
    margin: 0px;
}
.gpt3WhatGp-feature .gpt3__features-container__feature-text{
    max-width: 700px;
}
.gpt3WhatGp-header{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin: 4rem 0 2rem;
}
.gpt3WhatGp-header h1{
    font-size: 34px;
    line-height: 45px;
    font-family: var(--font-family);
    font-weight: 800;
    max-width: 510px;
}
.gpt3WhatGp-header p{
    font-size: 16px;
    line-height: 30px;
    font-family: var(--font-family);
    font-weight: 500;
    max-width: 510px;
    color: #FF8A71;
    cursor: pointer;
}
.gpt3WhatGp-container{
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    margin-top: 2rem;
}

/* Customizing Feature componentr as per need */
.gpt3WhatGp-container .gpt3__features-container__feature {
    flex: 1;
    margin: 1rem;
    min-width: 210px;
    display: unset;
    flex-direction: column;
}

/* Customizing feature component as per needs */
.gpt3WhatGp-container .gpt3__features-container_feature-text {
    margin-top: 0.5rem;
}

@media screen and (max-width: 850px) {
    .gpt3WhatGp-header {
        flex-direction: column;
        align-items: flex-start;
        margin-bottom: 0rem;
    }

    .gpt3WhatGp-header p {
        margin-top: 1rem;
    }
}

@media screen and (max-width: 650px) {
    /* Customizing feature component as per needs */
    .gpt3WhatGp-feature .gpt3__features-container__feature {
        flex-direction: column;
    }

    /* Customizing feature component as per needs */
    .gpt3WhatGp-feature .gpt3__features-container_feature-text {
        margin-top: 0.5rem;
    }

    .gpt3WhatGp-header h1 {
        font-size: 28px;
        line-height: 40px;
        
    }
}

@media screen and (max-width: 350px) {
    /* Customizing feature component as per needs */
    .gpt3WhatGp-container .gpt3__features-container__feature {
        margin: 1rem 0;
        min-width: 100%;
    }
}