.gpt3Header {
    display: flex;
}

.gpt3Header-content {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
    margin-right: 5rem;
}

.gpt3Header-content h1 {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 35px;
    line-height: 75px;
    letter-spacing: -0.04em;
}

.gpt3Header-content p {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 18px;
    line-height: 27px;
    color: #81AFDD;
    margin-top: 1.5rem;
}

.gpt3Header-content_input {
    width: 100%;
    margin: 2rem 0 1rem;
    display: flex;
}

.gpt3Header-content_input input {
    /* flex: 2; */
    flex: 2;
    width: 100%;
    min-height: 50px;
    background: #052D56;
    /* border-radius: 5px 0px 0px 5px; */
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
    font-family: var(--font-family);
    border: 2px solid var(--color-footer);
    padding: 0 1rem;
    outline: none;
    color: #fff;
    font-size: 15px;
    line-height: 27px;
}

.gpt3Header-content_input button {
    flex: 0.6;
    width: 100%;
    min-height: 50px;
    background: #FF4820;
    /* border-radius: 5px 0px 0px 5px; */
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
    font-family: var(--font-family);
    cursor: pointer;
    outline: none;
    padding: 0 1rem;
    border: none;
    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    line-height: 27px;
    color: #FFFFFF;
}

.gpt3Header-content__people {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    width: 100%;
    margin-top: 1rem;
}

.gpt3Header-content__people img {
    width: 189.71px;
    height: 38px;
}

.gpt3Header-content__people p {
    margin-left: 0 0 0 1rem;
    font-style: normal;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 12px;
    line-height: 38px;
    color: #FFFFFF;
    text-align: center;
}

.gpt3Header__image {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
}

.gpt3Header__image img {
    width: 100%;
    height: 100%;
}

@media screen and (max-width: 1050px) {
    .gpt3Header {
        flex-direction: column;
    }

    .gpt3Header-content {
        margin: 0 0 3rem;
    }
}

@media screen and (max-width: 650px) {
    .gpt3Header h1 {
        font-size: 48px;
        line-height: 60px;
    }

    .gpt3Header p {
        font-size: 16px;
        line-height: 24px;
    }
    
    .gpt3Header-content__people {
        flex-direction: column;
    }
    
    .gpt3Header-content__people p {
        margin: 0px;
    }
    
    .gpt3Header-content__input input,
    .gpt3Header-content__input button {
        font-size: 16px;
        line-height: 24px;
        
    }
}
@media screen and (max-wdith: 490px) {
    
    .gpt3Header h1 {
        font-size: 25px;
        line-height: 48px;
    }
    
    .gpt3Header p {
        font-size: 14px;
        line-height: 24px;
    }
    .gpt3Header-content__input input,
    .gpt3Header-content__input button {
        font-size: 14px;
        line-height: 24px;
        
    }
}