.gpt3Footer {
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background: var(--color-footer);
}

.gpt3Footer-heading {
    width: 100%;
    text-align: center;
    margin-bottom: 3rem;
}

.gpt3Footer-heading h1 {
    font-family: var(--font-family);
    font-size: 45px;
    font-weight: 800;
    line-height: 75px;
    letter-spacing: -0.04em;
    text-align: center;
}

.gpt3Footer-btn {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 1rem;
    border: 1px solid white;
    text-align: center;
    margin-bottom: 10rem;
}

.gpt3Footer-btn p {
    box-sizing: border-box;
    font-family: var(--font-family);
    font-size: 18px;
    line-height: 21px;
    letter-spacing: 0em;
    text-align: left;
    color: white;
    word-spacing: 2px;
}

.gpt3Footer-links {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: wrap;
    flex-direction: row;
    width: 100%;
    text-align: left;
}

.gpt3Footer-links div {
    width: 250px;
    margin: 1rem;
}

.gpt3Footer-links-logo {
    display: flex;
    flex-direction: column;
}

.gpt3Footer-links-logo img {
    width: 118px;
    height: 30px;
    margin-bottom: 1rem;
}

.gpt3Footer-links-logo p {
    font-family: var(--font-family);
    font-size: 12px;
    line-height: 14px;
    letter-spacing: 0em;
    text-align: left;
    color: white;
}

.gpt3Footer-links-div {
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
}

.gpt3Footer-links-div h4 {
    font-family: var(--font-family);
    font-size: 14px;
    line-height: 16px;
    text-align: left;
    color: white;
    margin-bottom: 1rem;
}

.gpt3Footer-links-div p {
    font-family: var(--font-family);
    font-size: 12px;
    line-height: 14px;
    letter-spacing: 0em;
    text-align: left;
    margin: 0.5rem 0;
    cursor: pointer;
}

.gpt3Footer-copyright {
    margin-top: 2rem;
    text-align: center;
    width: 100%;
}

.gpt3Footer-copyright p {
    font-family: var(--font-family);
    font-size: 12px;
    line-height: 14px;
    letter-spacing: 0em;
    text-align: left;
    color: white;
    /* font-feature-settings: "liga" off; */

}
@media screen and (max-width:850px){
    .gpt3Footer-heading h1{
        font-size: 44px;
        line-height: 50px;
    } 
}
@media screen and (max-width:550px){
    .gpt3Footer-heading h1{
        font-size: 34px;
        line-height: 42px;
    }
    .gpt3Footer-links-div{
        margin: 1rem 0;
    }
    .gpt3Footer-btn p{
        font-size: 14px;
        line-height: 20px;
    }
}