.gpt3Blog {
    display: flex;
    flex-direction: column;
}

.gpt3Blog-heading {
    width: 100%;
    text-align: left;
    margin-bottom: 5rem;
}

.gpt3Blog-heading h1 {
    font-family: var(--font-family);
    font-size: 62px;
    font-weight: 800;
    line-height: 75px;
    letter-spacing: -0.04em;
}

.gpt3Blog-container {
    display: flex;
    flex-direction: row;
}

.gpt3Blog-container-GroupA {
    flex: 0.75;
    margin-right: 2rem;
}

.gpt3Blog-container-GroupB {
    flex: 1;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 2rem;
}

@media screen and (max-width:990px) {
    .gpt3Blog-container {
        flex-direction: column-reverse;
    }

    .gpt3Blog-container-GroupA {
        margin: 2rem 0;
    }

    .gpt3Blog-container-GroupA .gpt3Blog-container-article {
        width: 48%;
    }

    .gpt3Blog-container-GroupA .gpt3Blog-container-article img {
        height: 250px;
    }
}

@media screen and (max-width:700px) {
    .gpt3Blog-container-GroupB {
        grid-template-columns: repeat(1, 1fr);
    }

    .gpt3Blog-container-GroupA .gpt3Blog-container-article {
        width: 100%;
    }
    .gpt3Blog-heading h1{
        font-size: 46px;
        line-height: 52px;
    }
    
}
@media screen and (max-width:550px) {
    .gpt3Blog-heading h1{
        font-size: 34px;
        line-height: 42px;
    }
    .gpt3Blog-container-GroupA .gpt3Blog-container-article {
        width: 100%;
    }
    .gpt3Blog{
        width: 100%;
    } 
}
