.gpt3Navbar{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 2rem 6rem;
    /* width: 100vh; */
}
.gpt3Navbar-links{
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}
.gpt3Navbar-links_logo{
    margin-right: 2rem;
}
.gpt3Navbar-links_logo img{
    width: 62px;
    height: 16px;
}
.gpt3Navbar-links_container{
    display: flex;
    flex-direction: row;
}
.gpt3Navbar-sign{
    display: flex;
    justify-content: flex-end;
    align-items: center;
}
.gpt3Navbar-links_container p, .gpt3Navbar-sign p, .gpt3Navbar-menu_container p{
    color: #fff;
    font-family: var(--font-family);
    font-size: 1.1rem;
    font-weight: 500;
    line-height: 25px;
    text-transform: capitalize;
    margin: 0rem 1rem;
    cursor: pointer;
}
.gpt3Navbar-sign button, .gpt3Navbar-menu_container button{
    padding: 0.5rem 1rem;
    margin-right: 15px;
    font-family: var(--font-family);
    color: white;
    font-size: 18px;
    font-weight: 500;
    line-height: 25px;
    border: none;
    outline: none;
    cursor: pointer;
    background: #FF4820;
    border-radius: 5px;
}
.gpt3Navbar-menu{
    margin-left: 1rem;
    display: none;
    position: relative;
}
.gpt3Navbar-menu svg{
    cursor: pointer;
}
.gpt3Navbar-menu_container{
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    flex-direction: column;
    text-align: end;
    background: var(--color-footer);
    padding: 4rem;
    position: absolute;
    top: 40px;
    right: 0px;
    margin-top: 1rem;
    min-width: 210px;
    border-radius: 5px;
    box-shadow: 0 0 5 rgba(0,0,0, 0.2);
}
.gpt3Navbar-menu_container p{
    margin: 1rem 0rem;
}
.gpt3Navbar-menu_container-links-sign{
    display: none;
}
@media screen and (max-width: 1050px) {
    .gpt3Navbar-links_container{
        display: none;
    }
    .gpt3Navbar-menu{
        display: flex;
    }
}
@media screen and (max-width: 700px) {
   .gpt3Navbar{
    padding: 2rem 4rem;
   }
}
@media screen and (max-width: 550px) {
   .gpt3Navbar{
    padding: 2rem;
   }
   .gpt3Navbar-sign{
    display: none;
   }
   .gpt3Navbar-menu_container{
    top: 20px;
   }
   .gpt3Navbar-menu_container-links-sign{
    display: block;
   }
}
