.gpt3Possibility {
    display: flex;
    flex-direction: row;
}

.gpt3Possibility-image {
    display: flex;
    flex: 1;
    justify-content: flex-start;
    align-items: center;
    margin-right: 2rem;
}

.gpt3Possibility-image img {
    width: 100%;
    height: 100%;
}

.gpt3Possibility-content {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: flex-start;
}

.gpt3Possibility-content h4 {
    font-family: var(--font-family);
    font-size: 16px;
    font-weight: 500;
    line-height: 30px;
    letter-spacing: 0em;
    text-align: left;
    color: rgba(113, 229, 255, 1);

}

.gpt3Possibility-content h4:last-child {
    font-family: Manrope;
    font-size: 16px;
    font-weight: 500;
    line-height: 30px;
    letter-spacing: 0em;
    text-align: left;
    color: rgba(255, 138, 113, 1);
}

.gpt3Possibility-content h1 {
    font-family: Manrope;
    font-size: 24px;
    font-weight: 800;
    line-height: 45px;
    letter-spacing: 0em;
    text-align: left;
}

.gpt3Possibility-content p {
    font-family: Manrope;
    font-size: 16px;
    font-weight: 400;
    line-height: 30px;
    letter-spacing: 0em;
    text-align: left;
    color: rgba(129, 175, 221, 1);
    margin-bottom: 0.5rem;

}
@media screen and (max-width: 990px) {
    .gpt3Possibility{
        flex-direction: column;
    }
    .gpt3Possibility-image{
        margin: 1rem 0;
    }
    .gpt3Possibility-content{
        margin-top: 2rem;
    }
    
}